body {
  font: 14px "Century Gothic", Futura, sans-serif;
  /*margin: 20px;*/
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.appWrapper {
  width: 50%;
  min-width: 200px;
  padding: 0% 5%;
}
div.appContent .profilePreview {
	height: 85vh;
	padding: 20px;
	box-shadow: 0px 2px 10px 5px #b3b3b3;
	border-radius: 10px;
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	z-index: -2;
	color: white;
}

div.appContent .profilePreview .backgroundOverlay {
 	border-radius: 10px;
	position: absolute;
	background: #000;
	opacity: 0.5;
	height: 100%;
	width: 100%;
	left: 0;
	top: 0;
	z-index: -1;
}

div.profilePreview .avatar {
	/* display: table; */
	width: 122px;
	height: 122px;
	margin: 10px 0px;
}

div.profilePreview .avatar img {
	/* display: table; */
	overflow: hidden;
}

div.profilePreview .username {
	margin: 5px 0px;
	font-size: 1.4em;
}

div.profilePreview .email {
	font-size: 1em;
}

div.profilePreview .username,
div.profilePreview .email {
	text-align: center;
	width: 100%;
	word-wrap: break-word;
}

div.profilePreview .intro {
	width: 100%;
	margin: 20px 0px;
	font-size: 1.1em;
	text-align: justify;
}

div.profilePreview .avatar .circle {
	border-radius: 50%;
	display: flex;
	overflow: hidden;
	justify-content: center;
	align-items: center;
}

div.profilePreview .avatar .losange {
	-webkit-transform: rotate(45deg) translateY(12px);
	        transform: rotate(45deg) translateY(12px);
}

div.profilePreview .avatar .losange, .losange div {
    -webkit-transform-origin: 52% 93%;
            transform-origin: 52% 93%;
    overflow: hidden;
    width: 86px;
    height: 86px;
}

.losange .los1 {
    width: 122px;
    height: 122px;
    -webkit-transform: rotate(-45deg) translateY(-12px) translateX(38px);
            transform: rotate(-45deg) translateY(-12px) translateX(38px);
}

div.profilePreview .avatar .diamond {
    -webkit-transform: rotate(45deg) translateY(-9px) translateX(4px);
            transform: rotate(45deg) translateY(-9px) translateX(4px);
}

div.profilePreview .avatar .diamond .dia {
    width: 122px;
    height: 122px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

div.profilePreview .avatar .diamond {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    overflow: hidden;
    width: 105px;
    height: 105px;
}

div.profilePreview .hexa {
    width: 170px;
    height: 100px;
    -webkit-transform: rotate(120deg) translateX(25px) translateY(12px);
            transform: rotate(120deg) translateX(25px) translateY(12px);
}

div.profilePreview .hexa div {
    width: 100%;
    height: 100%;
}

div.profilePreview .hexa, .hexa div {
    margin: 0 auto;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    overflow: hidden;
}

div.profilePreview .hex1 {
    -webkit-transform: rotate(-60deg);
            transform: rotate(-60deg);
}

div.profilePreview .hex2 {
    -webkit-transform: rotate(-60deg);
            transform: rotate(-60deg);
}

div.profilePreview .hexa img {
    position: relative;
	top: -15px;
    left: 24px;
}

div.profilePreview .octo {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

div.profilePreview .octo, .octo div {
    margin: 0 auto;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    overflow: hidden;
    width: 122px;
    height: 122px;
}

div.profilePreview .octo1 {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

div.profilePreview .hobbies {
    display: flex;
    flex-wrap: wrap;
    max-width: 100%;
    justify-content: center;
}

div.profilePreview .hobby {
    margin: 5px;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid white;
    box-shadow: 0px 0px 10px 0px #b3b3b3;
    -webkit-animation: fadein 1s ease-out;
            animation: fadein 1s ease-out;
    background-color: #00000060;
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}
body {
  margin: 10px 20px;
  font-size: 14px;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}

body * {
  box-sizing: border-box;
}

div.app .appTitle{
  text-align: center;
}

div.appContent {
  display: flex;
  justify-content: space-between;
}

div.profileForm form * {
  display: flex;
}

input[name="username"],
input[name="email"],
input[name="password"],
select[name="shape"],
.hobbyFakeInput {
  width: 100%;
  height: 40px;
  padding: 0 5px;
  border-radius: 5px;
  border: 2px solid #d1d1d1;
  box-sizing: border-box;
  font-size: 14px;
  font-family: "Roboto", sans-serif;
  color: #252525;
  background-color: #fff;
}

textarea[name="intro"] {
  resize: none;
  padding: 5px;
  border-radius: 5px;
  border: 2px solid #d1d1d1;
  box-sizing: border-box;
  font-size: 14px;
  font-family: "Roboto", sans-serif;
  color: #252525;
  background-color: #fff;
}

div.profileForm form .error {
  background-color: rgba(255, 0, 0, 0.15);
  box-shadow: 0px 0px 8px 0px #ffdcdc;
  color: red;
  font-size: 0.9em;
  text-align: center;
  padding: 5px 10px;
  margin: 0 auto;
  border-radius: 0px 0px 20px 20px;
  justify-content: center;
}

div.profileForm form > div {
  display: flex;
  flex-direction: column;
  padding: 5px 0px;
}

div.profileForm form input,
div.profileForm form textarea,
div.profileForm form select,
.hobbySelector {
  margin-top: 5px;
}

#username,
#email,
#intro,
#background {
  margin-bottom: 5px;
}

input[type="submit"] {
  cursor: pointer;
  border-radius: 5px;
  padding: 5px;
  font-weight: 600;
}

input:required {
  box-shadow:none;
}

input[type="submit"].submit-error {
  -webkit-animation: circle-fade-error 1s linear;
          animation: circle-fade-error 1s linear;
}

input[type="submit"].submit-success {
  -webkit-animation: circle-fade-success 1s linear;
          animation: circle-fade-success 1s linear;
}


@-webkit-keyframes circle-fade-error {
  0%   {box-shadow: 0px 0px 0px 0px red;}
  10%  {box-shadow: 1px 1px 1px 1px red;}
  20%  {box-shadow: 1px -1px 1px 1px red;}
  30%  {box-shadow: -1px -1px 1px 1px red;}
  40%  {box-shadow: -1px 1px 1px 1px red;}
  50%  {box-shadow: 1px 1px 1px 1px red;}
  80%  {box-shadow: 0px 0px 0px 0px red;}
  90%  {box-shadow: 0px 0px 5px 2px red;}
  100% {box-shadow: 0px 0px 0px 0px red;}
}


@keyframes circle-fade-error {
  0%   {box-shadow: 0px 0px 0px 0px red;}
  10%  {box-shadow: 1px 1px 1px 1px red;}
  20%  {box-shadow: 1px -1px 1px 1px red;}
  30%  {box-shadow: -1px -1px 1px 1px red;}
  40%  {box-shadow: -1px 1px 1px 1px red;}
  50%  {box-shadow: 1px 1px 1px 1px red;}
  80%  {box-shadow: 0px 0px 0px 0px red;}
  90%  {box-shadow: 0px 0px 5px 2px red;}
  100% {box-shadow: 0px 0px 0px 0px red;}
}

@-webkit-keyframes circle-fade-success {
  0%   {box-shadow: 0px 0px 0px 0px green;}
  10%  {box-shadow: 1px 1px 1px 1px green;}
  20%  {box-shadow: 1px -1px 1px 1px green;}
  30%  {box-shadow: -1px -1px 1px 1px green;}
  40%  {box-shadow: -1px 1px 1px 1px green;}
  50%  {box-shadow: 1px 1px 1px 1px green;}
  80%  {box-shadow: 0px 0px 0px 0px green;}
  90%  {box-shadow: 0px 0px 5px 2px green;}
  100% {box-shadow: 0px 0px 0px 0px green;}
}

@keyframes circle-fade-success {
  0%   {box-shadow: 0px 0px 0px 0px green;}
  10%  {box-shadow: 1px 1px 1px 1px green;}
  20%  {box-shadow: 1px -1px 1px 1px green;}
  30%  {box-shadow: -1px -1px 1px 1px green;}
  40%  {box-shadow: -1px 1px 1px 1px green;}
  50%  {box-shadow: 1px 1px 1px 1px green;}
  80%  {box-shadow: 0px 0px 0px 0px green;}
  90%  {box-shadow: 0px 0px 5px 2px green;}
  100% {box-shadow: 0px 0px 0px 0px green;}
}

div#background {
  display: inline-block;
}



.arrow-down {
  width: 0; 
  height: 0; 
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-top: 15px solid black;
  opacity: 0.5;
  cursor: pointer;
}

.arrow-down:hover {
  opacity: 0.8;
}

div.profileForm .imageSelector {
  display: inline-flex;
  flex-direction: row;
  text-align: center;
  flex-wrap: wrap;
  border-radius: 5px;
  background-color: #f9f9f9;
  padding: 10px;
  margin: 5px 0px;
  justify-content: space-evenly;
  box-shadow: 1px 1px 8px 0px #b3b3b3;
}

div.profileForm .imageSelector .activeImage {
  display: flex;
  flex-direction: row;
  padding: 0px 5px;
  align-items: center;
}

div.profileForm .imageSelector * {
  display: inline-block;;
}

div.profileForm .imageSelector img {
  cursor: pointer;
  display: inline-block;
  height: 70px;
  width: 70px;
  margin: 5px;
  opacity: 0.5;
  border-radius: 5px;
}

div.profileForm .imageSelector img:hover {
  border-radius: 5px;
  /*background-color: black;*/
  opacity: 1;
}

div.profileForm .imageSelector img.active {
  opacity: 1;
}


div.profileForm .imageSelector .availableImages,
div.profileForm .hobbySelector .hobbiesList {
  transition: opacity 0.15s;
}

div.profileForm .hobbySelector {
  flex-direction: column;
}

div.profileForm .hobbyFakeInput {
  flex-wrap: wrap;
  padding: 5px;
  height: auto;
  min-height: 40px;
  padding: 0px 0px 5px 5px;
}

div.profileForm .hobbyFakeInput.focused {
  border: 3px solid #8eb0e7;
}

div.profileForm .hobbyFakeInput .hobbiesSelected {
  align-items: center;
  flex-wrap: wrap;
}

div.profileForm .hobbyFakeInput .hobbyBox {
  padding: 5px;
  background-color: rgba(22, 255, 0, 0.09);
  border-radius: 2px;
  border: 1px solid rgba(34, 255, 0, 0.4);
  color: #20b61c;
  margin: 5px 5px 0px 0px;
  align-items: center;
  text-align: center;
}

div.profileForm .hobbyFakeInput .hobbyBox .removeHobby {
  margin-right: 5px;
  padding: 0px 5px 0px 0px;
  border-right: 1px solid #20b61c;
  cursor: pointer;
  transition: color 0.25s ease-out;
}

div.profileForm .hobbyFakeInput .hobbyBox .removeHobby:hover {
  color: red;
}

div.profileForm .hobbyInput {
  flex: 1 1;
  padding: 5px 0px 0px 0px;
}

div.profileForm .hobbyFakeInput input {
  background: none transparent;
  border: 0 none;
  box-shadow: none;
  display: flex;
  flex: 1 1;
  font-family: inherit;
  font-size: inherit;
  margin: 0;
  outline: none;
}

div.profileForm .hobbySelector .hobbiesList {
  margin: 0;
  padding: 0;
}
div.profileForm .hobbySelector .hobbiesList ul {
  display: flex;
  flex-direction: column;
  font-size: 1em;
  font-weight: 600;
  margin: 0;
  padding: 0;
  width: 100%;
}

div.profileForm .hobbySelector .hobbiesList ul li {
  align-items: center;
  border: 0 solid #d1d1d1;
  border-width: 1px 2px;
  padding: 5px 10px;
  cursor: pointer;
  justify-content: space-between;
  transition: background-color 0.5s ease;
}

div.profileForm .hobbySelector .hobbiesList ul li:hover {
  background-color: #ddefff;
}

div.profileForm .hobbySelector .hobbiesList ul li .addHobby {
  font-size: 1.5em;
  transition: color 0.5s ease;
}

div.profileForm .hobbySelector .hobbiesList ul li:hover .addHobby {
  color: green;
}

